import React, { useState } from 'react';
import jstat from 'jstat';
import { LineChart, Line, XAxis, YAxis, Legend, Tooltip, CartesianGrid} from 'recharts';

const KeyGenerator = () => {
   
    const [keyValue,setKeyValue] = useState(0)
    const [bloknote,setBloknote] = useState(false)
    let [bloknotKey,setBloknotKey] = useState([])
   
    const [val1,setVal1] = useState(0)
    const [val2,setVal2] = useState(0)
    const [val3,setVal3] = useState(0)

    const [itt1,setItt] = useState(0)
    const [err1,setErr] = useState(0)


    let [notClearKey,setNotClearKey] = useState([])
    let [—ÅlearKey,setClearKey] = useState([])
    const [speed,setSpeed] = useState(0)

    const [speedLazer,setSpeedLazer] = useState(500)
    const [speedLazer1,setSpeedLazer1] = useState(500)

    const [data,setData] = useState([{'—Å–∫–æ—Ä–æ—Å—Ç—å –ø–µ—Ä–µ–¥–∞—á–∏ –∫–ª—é—á–∞': 0, '–ø—Ä–∞–≤–∏–ª—å–Ω–æ –ø—Ä–∏–Ω—è—Ç—ã–µ —Ñ–æ—Ç–æ–Ω—ã': 0, '–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏–∫–ª–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–ª—é—á–∞ –∑–∞–¥–∞–Ω–Ω–æ–π –¥–ª–∏–Ω—ã': 0}])
    
  

    function keyCount() {
      
        
        let key = []
        let clearKeyArray = []
        let countItt = 0
        let countErr = 0
      
        while(clearKeyArray.length < keyValue) {
           
            let itt = jstat.poisson.sample(0.00000003) 
            if(itt === 1) {
                key.push('ITT')
                countItt++
                continue;  
            }

            let err = jstat.poisson.sample(0.0000003) 
            if(err === 1) {
                key.push('ERR')
                countErr++
                continue;  
            }

           let poison1 = jstat.poisson.sample(0.001) 
            if(poison1 === 0) {
                key.push('_')
            
                continue;  
            }

           let stroka1 = (Math.trunc(Math.random()*2) === 0 ? 0 : 'œÄ/2')
           let stroka2 = (stroka1 === 'œÄ/2'? 1 : 0)
           let stroka3 = (Math.trunc(Math.random()*2) === 0? 0 : '3œÄ/2')
           let stroka4 = (stroka3 === '3œÄ/2'? 1 : 0)
           let stroka5

                if(stroka1 === 0 && stroka3 === 0) {
                    stroka5 = 0
                }
                if(stroka1 === 'œÄ/2' && stroka3 === 0 || stroka1 === 0 && stroka3 === '3œÄ/2') {
                    stroka5 ='œÄ/2'
                }
                if(stroka1 === 'œÄ/2' && stroka3 === '3œÄ/2') {
                    stroka5 ='œÄ'
                }
            
            let stroka6 = (stroka5 === 0 ? '100%' : stroka5 === 'œÄ'? 0 : '50%' )
            let stroka7 = (stroka5 === 0 ? 0 : stroka5 === 'œÄ'? '100%' : '50%' )
            let stroka8 = (stroka7 === 0 ? 0 : stroka7 === '100%' ? 1 : '-' )
            key.push(stroka8)

            if(stroka8 === 0 || stroka8 === 1 ) {
                clearKeyArray.push(stroka8)
            }
        
        }
      
        setNotClearKey(key)
        setClearKey(clearKeyArray)
      
        setBloknotKey([...bloknotKey, [...clearKeyArray]])
        setVal1(key.length-clearKeyArray.length)
        setVal2(clearKeyArray.length)
        console.log(speedLazer);
        let a = speedLazer*1000000
        
        setVal3(1/a*key.length.toFixed(8))
        setItt(countItt)
        setErr(countErr)
        setSpeed((1/(1/a*key.length*1000)).toFixed(2))
        let arr = [...data, {'—Å–∫–æ—Ä–æ—Å—Ç—å –ø–µ—Ä–µ–¥–∞—á–∏ –∫–ª—é—á–∞': (1/(key.length/500000000*1000)).toFixed(2), '–ø—Ä–∞–≤–∏–ª—å–Ω–æ –ø—Ä–∏–Ω—è—Ç—ã–µ —Ñ–æ—Ç–æ–Ω—ã': clearKeyArray.length, '–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏–∫–ª–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–ª—é—á–∞ –∑–∞–¥–∞–Ω–Ω–æ–π –¥–ª–∏–Ω—ã': key.length}]
   
        setData(arr)
    }

      

    return (
        <div className="shop" >
         
              
         –í—ã–±–µ—Ä–∏—Ç–µ –¥–ª–∏–Ω—É –∫–ª—é—á–∞: <input type='number' onChange={(e) => setKeyValue(e.target.value)}/> –±–∏—Ç   <button onClick={keyCount}> —Å–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞—Ç—å –∫–ª—é—á</button><br/>
         –í—ã–±–µ—Ä–∏—Ç–µ —Å–∫–æ—Ä–æ—Å—Ç—å –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ –∏–º–ø—É–ª—å—Å–æ–≤ –ª–∞–∑–µ—Ä–æ–º: <input type='number' onChange={(e) => setSpeedLazer(e.target.value)}/> M–ì—Ü  <button onClick={keyCount}> –æ–∫ </button>
         <LineChart
                    width={400}
                    height={400}
                    data={data}
                    margin={{ top: 5, right: 20, left: 10, bottom: 5 }}
                    >
                        
                     {/* <XAxis dataKey="—Å–∫–æ—Ä–æ—Å—Ç—å –ø–µ—Ä–µ–¥–∞—á–∏ –∫–ª—é—á–∞" yAxisId={1}/> */}
                     {/* <XAxis dataKey="–ø—Ä–∞–≤–∏–ª—å–Ω–æ –ø—Ä–∏–Ω—è—Ç—ã–µ —Ñ–æ—Ç–æ–Ω—ã" /> */}
                     <Legend />
                    <Tooltip />
                    <CartesianGrid stroke="#f5f5f5" />
                    <Line type="monotone" dataKey="–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏–∫–ª–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–ª—é—á–∞ –∑–∞–¥–∞–Ω–Ω–æ–π –¥–ª–∏–Ω—ã" stroke="#111111" yAxisId={1} />
                    <Line type="monotone" dataKey="–ø—Ä–∞–≤–∏–ª—å–Ω–æ –ø—Ä–∏–Ω—è—Ç—ã–µ —Ñ–æ—Ç–æ–Ω—ã" stroke="#ff7300" yAxisId={1} />
                    <Line type="monotone" dataKey="—Å–∫–æ—Ä–æ—Å—Ç—å –ø–µ—Ä–µ–¥–∞—á–∏ –∫–ª—é—á–∞" stroke="#111fff" yAxisId={1} />
                </LineChart>  
        <h4> —Å–∫–æ—Ä–æ—Å—Ç—å –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ –∏–º–ø—É–ª—å—Å–æ–≤ –ª–∞–∑–µ—Ä–æ–º = {speedLazer} M–ì—Ü</h4>
        <h4> –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–æ—Ç–µ—Ä—è–Ω–Ω—ã—Ö —Ñ–æ—Ç–æ–Ω–æ–≤ = {val1}</h4>
        <h4> –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø—Ä–∞–≤–∏–ª—å–Ω–æ –ø—Ä–∏–Ω—è—Ç—ã—Ö —Ñ–æ—Ç–æ–Ω–æ–≤ = {val2}</h4>
        <h4> –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏–∫–ª–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–ª—é—á–∞ –∑–∞–¥–∞–Ω–Ω–æ–π –¥–ª–∏–Ω—ã= {val2+val1}</h4>
        <h4> –∫–ª—é—á —Å–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞–Ω –∑–∞ {val3.toFixed(9)} —Å</h4>
        <h4> —Å–∫–æ—Ä–æ—Å—Ç—å –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ –∫–ª—é—á–∞ {speed} –∫–±–∏—Ç/c</h4>
        <h4> –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –æ—à–∏–±–æ–∫ (–∏–∑-–∑–∞ –ò–¢–¢) = {itt1}</h4>
        <h4> –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –Ω–µ–æ–ø—Ä–µ–¥–µ–ª—è–µ–º—ã—Ö —Ñ–æ—Ç–æ–Ω–æ–≤ = {err1}</h4> 
        <div className='key'>  <h4>—Å—ã—Ä–æ–π –∫–ª—é—á = </h4> {notClearKey.map((i,index) => (index%110===0 && index!==0)?<>{i} <br/></>: i)} </div>  
        <div className='key'>  <h4>–ø—Ä–æ—Å–µ—è–Ω–Ω—ã–π –∫–ª—é—á = {—ÅlearKey}</h4>  </div> 
       
                <div style={{'fontSize': '25px', 'fontWeight': 900}}>–ë–ª–æ–∫–Ω–æ—Ç –∫–ª—é—á–µ–π</div> 
                <button onClick={()=>setBloknote(true)}> –ø–æ–∫–∞–∑–∞—Ç—å –±–ª–æ–∫–Ω–æ—Ç</button>
                <button onClick={()=>setBloknote(false)}> —Å–∫—Ä—ã—Ç—å –±–ª–æ–∫–Ω–æ—Ç</button><br/>
       
        
          
        <div className={bloknote === true ?'bloknot' : 'bloknotNone'  }>
        {bloknotKey.map((i) => (<>{i}<br/></>))}
        </div>
         
        </div>
    );
};

export default KeyGenerator;