import React, { useState } from 'react';
import './index.css'




const Encription = () => {
    
    const [value1,setValue1] = useState([0,0,0,0,0,0,0,0])
    const [value2,setValue2] = useState([0,0,0,0,0,0,0,0])
    const [value3,setValue3] = useState([0,0,0,0,0,0,0,0])
    const [value4,setValue4] = useState([0,0,0,0,0,0,0,0])
    const [value5,setValue5] = useState([0,0,0,0,0,0,0,0])
    const [value6,setValue6] = useState([0,0,0,0,0,0,0,0])
    const [value7,setValue7] = useState([0,0,0,0,0,0,0,0])
    const [value8,setValue8] = useState([0,0,0,0,0,0,0,0])

    


    let stroka1
    let stroka2
    let stroka3
    let stroka4
    let stroka5 =[]
    let stroka6
    let stroka7 =[]
    let stroka8



    function generateKey() {

        
        stroka1 = value1.map((i)=> {
            let a = Math.trunc(Math.random()*2)
            return a === 0? 0 : 'œÄ/2'
        })
        setValue1(stroka1)
        stroka2 = setTimeout(()=> setValue2(stroka1.map((i)=> i === 'œÄ/2'? 1 : 0)),300)
        stroka3 = value3.map((i)=> {
            let a = Math.trunc(Math.random()*2)
            return a === 0? 0 : '3œÄ/2'
        })
        setTimeout(()=>setValue3(stroka3),600)
        stroka4 = setTimeout(()=> setValue4(stroka3.map((i)=> i === '3œÄ/2'? 1 : 0)),900)


        for(let i = 0; i<8; i++) {
            if(stroka1[i] === 0 && stroka3[i] === 0) {
                stroka5.push(0)
            }
            if(stroka1[i] === 'œÄ/2' && stroka3[i] === 0 || stroka1[i] === 0 && stroka3[i] === '3œÄ/2') {
                stroka5.push('œÄ/2')
            }
            if(stroka1[i] === 'œÄ/2' && stroka3[i] === '3œÄ/2') {
                stroka5.push('œÄ')
            }
        }
        setTimeout(()=>setValue5(stroka5),1200)
        stroka6 = setTimeout(()=> setValue6(stroka5.map((i)=> i === 0 ? '100%' : i === 'œÄ'? 0 : '50%' )),1500)
        stroka7 = stroka5.map((i)=> i === 0 ? 0 : i === 'œÄ'? '100%' : '50%' )
        setTimeout(()=> setValue7(stroka7),1800)
        stroka8 = setTimeout(()=> setValue8(stroka7.map((i)=> i === 0 ? 0 : i === '100%' ? 1 : '-' )),2100)
    }
    return (
        <>
        <h4>–†–∞—Å–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ/–ø–µ—Ä–µ–¥–∞—á–∞ –∫–ª—é—á–∞ –ø–æ –ø—Ä–æ—Ç–æ–∫–æ–ª—É B92</h4>
        <button onClick={generateKey}> —Å–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞—Ç—å –∫–ª—é—á</button>
        <div className='enc'>
            <div className='enc_content title'>–§–∞–∑–æ–≤—ã–π —Å–¥–≤–∏–≥ –æ—Ç–ø—Ä–∞–≤–∏—Ç–µ–ª—è –ê–ª–∏—Å–∞ –§A</div>
                {value1.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>–ó–Ω–∞—á–µ–Ω–∏—è –±–∏—Ç–∞ –æ—Ç–ø—Ä–∞–≤–∏—Ç–µ–ª—è –ê–ª–∏—Å–∞</div>
                {value2.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>–§–∞–∑–æ–≤—ã–π —Å–¥–≤–∏–≥ –ø–æ–ª—É—á–∞—Ç–µ–ª—è –ë–æ–± –§B</div>
                {value3.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>–ó–Ω–∞—á–µ–Ω–∏—è –±–∏—Ç–∞ –ø–æ–ª—É—á–∞—Ç–µ–ª—è –ë–æ–±</div>
                {value4.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>–†–∞–∑–Ω–æ—Å—Ç—å —Ñ–∞–∑ –§A-–§B</div>
                {value5.map((i) => <div className='enc_content'>{i}</div>)}
            <div className='enc_content title'>–í–µ—Ä–æ—è—Ç–Ω–æ—Å—Ç—å –ø—Ä–∏—ë–º–∞ —Ñ–æ—Ç–æ–Ω–∞ 1-–º –æ–¥–Ω–æ—Ñ–æ—Ç–æ–Ω–Ω—ã–º —Ñ–æ—Ç–æ–¥–µ—Ç–µ–∫—Ç–æ—Ä–æ–º </div>
                {value6.map((i) => <div className='enc_content a'>{i}</div>)}
            <div className='enc_content title'>–í–µ—Ä–æ—è—Ç–Ω–æ—Å—Ç—å –ø—Ä–∏—ë–º–∞ —Ñ–æ—Ç–æ–Ω–∞ 2-–º –æ–¥–Ω–æ—Ñ–æ—Ç–æ–Ω–Ω—ã–º —Ñ–æ—Ç–æ–¥–µ—Ç–µ–∫—Ç–æ—Ä–æ–º</div>
                {value7.map((i) => <div className='enc_content a'>{i}</div>)}
            <div className='enc_content title'>–ë–∏—Ç –∫–ª—é—á–∞</div>
                {value8.map((i) => <div className={(i==='-') ? 'enc_content c': 'enc_content b'}>{i}</div>)}
        
        </div>
        
        </>
    );
};

export default Encription;